$(".filter__show").click(function () {
    $(this).parents(".filter__group").toggleClass("_active")
    $(this).siblings(".filter__hidden").slideToggle()
})


$(".filter__group").each(function (index, el){
    if($(el).hasClass("_active")){
        $(el).find(".filter__hidden").css("display","block")
    }
});

$(".catalog__filter-toggle").click(function () {
    $(".catalog__sidebar").addClass("_active")
    $("body").addClass("fixed-body")
})

$(".filter__close").click(function () {
    $(".catalog__sidebar").removeClass("_active")
    $("body").removeClass("fixed-body")
})


function isSailCardChecked(){
    let isChecked = true
    $(".catalog-card__select input").each(function (index, el){
        let checked = $(el).prop("checked")
        console.log(checked)
        if(!checked){
            isChecked = false
        }
    });
    return isChecked
}

isSailCardChecked()

$(".catalog-card__select input").change(function () {
    if(isSailCardChecked()){
        $(".sail__all input").prop("checked",true)
    } else{
        $(".sail__all input").prop("checked",false)
    }
})

$(".sail__all input").change(function () {
    let checked = $(this).prop("checked")
    $(".catalog-card__select input").each(function (index, el){
        $(el).prop("checked",checked)
    });
})
